import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { AppConstants } from '../config/app.constant';

export const API_URL = AppConstants.baseURL;

@Injectable({
  providedIn: 'root'
})
export class BanzpayService {

  constructor(private http: HttpClient) { 
 
  }
  getBenzpay(pageNo, pageSize,search) {
    return this.http.get<any>(`${API_URL}/banzpay-api/fetchInfo?page=${pageNo}&size=${pageSize}&search=${search}`);  
  }
  getBenzpayBasedOnStatus(status) {
    return this.http.get<any>(`${API_URL}/banzpay-api/fetchInfo?page=${0}&size=${300}&status=${status}`);  
  }
}
