import { ChangeDetectorRef, Component, OnInit } from "@angular/core";
import { from, Subject } from "rxjs";
import { ChargeMaintenanceService } from "src/app/shared/services/charge-maintenance-service.service";
import jsPDF from "jspdf";
import  "jspdf-autotable";
import Swal from 'sweetalert2';
import { getTime } from "date-fns";
import { BanzpayExcelService } from "src/app/shared/services/banzpay-excel.service";
import { BanzpayService } from "src/app/shared/services/banzpay.service";
import { HttpClient } from "@angular/common/http";
import { AppConstants } from "src/app/shared/config/app.constant";
// import { BanzpayExcelService } from "src/app/shared/services/banzpay-excel.service";
@Component({
  selector: "npr-banzpay-sql-data",
  templateUrl: "./banzpay-sql-data.component.html",
  styleUrls: ["./banzpay-sql-data.component.scss"],
})
export class BanzpaySqlDataComponent implements OnInit {
 
  data: any;
  dtOptions: DataTables.Settings = {};
  respArray: any[] = [];
  rowData: string[][];
  formattedHeaders: string[];
  exportType: string[] = ["Excel", "Pdf"];
  statusFilter: string[] = ["OPEN","PROCESSING","INPROGRESS","COMPLETED","FAILED"];
  respArrayCols: string[];
  dtTrigger: Subject<any> = new Subject<any>();
  totalPages: any;
  totalRecords: any;
  API_URL = AppConstants.baseURL;
  dataFilter: any;
  respArrayColsFilter: any;
  formattedHeadersFilter: string[];
  respArrayFilter: any;
  rowDataFilter: any[][];
  shouldRenderTable: boolean;
  columns: any;
  columnNames: any;
  rows: any;
  empty: boolean;
  constructor(
    private http: HttpClient,
    private cdr: ChangeDetectorRef,
    private excelService: BanzpayExcelService,
    private banzpayService: BanzpayService
  ) {}

  // ngOnInit():void {
  //   this.dtOptions = {
  //     pagingType: "full_numbers",
  //     pageLength: 5,
  //     processing: true,
  //     lengthMenu: [
  //       [5, 10, 20, 30, -1],
  //       [5, 10, 20, 30, "ALL"],
  //     ],
  //     retrieve: true,
 
  //   };
  //   this.getSqlDataSummary();
  // }
  async ngOnInit(): Promise<void> {
    this.fetchBanzpayInfoList(0, 5,'').subscribe((resp: any) => {
      this.shouldRenderTable = true;
      this.columns = resp.columnNames.filter((name) => name !== 'data').map((name) => ({
        id: name,
        name,
      }));
      this.columnNames = resp.columnNames.filter((name) => name !== 'data');

      this.dtOptions = {
              pagingType: "full_numbers",
              pageLength: 5,
              serverSide: true,
               processing: true,
              lengthMenu: [
                [5, 10, 20, 30, -1],
                [5, 10, 20, 30, "ALL"],
              ],
              retrieve: true,
        // pagingType: 'full_numbers',
        // pageLength: 10,
        // serverSide: true,
        // processing: true,
        ajax: (dataTablesParameters: any, callback) => {
          
          this.empty=false;
          dataTablesParameters.minNumber = dataTablesParameters.start + 1;
          dataTablesParameters.maxNumber =
            dataTablesParameters.start + dataTablesParameters.length;

          const pageSize = dataTablesParameters.length;
          const pageNo = Math.floor(dataTablesParameters.minNumber / pageSize);
          const search=dataTablesParameters.search.value
          this.fetchBanzpayInfoList(pageNo, pageSize , search).subscribe(
            (paginatedResponse: any) => {
              if(paginatedResponse){
              this.rows = paginatedResponse.columnData.banzpayInfoList;
              if(this.rows.length!=0){
                callback({
                  recordsTotal: paginatedResponse.columnData.totalRecords,
                  recordsFiltered: paginatedResponse.columnData.totalRecords,
                  data: [],
                });
              }
             
              if(this.rows.length==0){
                callback({
                  recordsTotal: 0,
                  recordsFiltered: 0,
                  data: [],
                });
              }
            }
            // else{
            
            //   this.empty=true;
            //   this.rows=[];
            //   return;
            // }
          }
          );
        },
        columns: resp.columnNames
          .filter((name) => name !== 'data')
          .map((name) => ({ data: name })),
      };
    });
  }

  fetchBanzpayInfoList = (pageNo, pageSize , search) => this.banzpayService.getBenzpay(pageNo, pageSize, search);

  getData = (row, columnName) => row[columnName];
  // getSqlDataSummary() {
  //   this.banzpayService.getBenzpay(pageNo, pageSize).subscribe((resp) => {
  //     console.log(resp);
  //     this.data = resp;
  //     this.respArrayCols = this.data.columnNames;
  //     this.getSqlData(this.data);
  //   });
  // }

  ngOnDestroy(): void {
    this.dtTrigger.unsubscribe();
  }

  async getSqlData(data) {
    ;
    await new Promise((resolve) => setTimeout(resolve, 0));
    this.respArrayCols = data.columnNames.filter(function (value, index, arr) {
      return value != "data";
    });
    // this.respArrayCols.splice(1, 1);
    this.formattedHeaders = this.respArrayCols.map((item) => {
      return item.replace(/([A-Z])/g, " $1").replace(/^./, function (str) {
        return str.toUpperCase();
      });
    });
    this.respArray = data.columnData.banzpayInfoList.map((item) => {
      const { data, ...rest } = item;
      return rest;
    });
    this.constructDynamicRows();
      this.dtTrigger.next();
   
  }
  constructDynamicRows() {
    this.rowData = this.respArray.map((data) => {
      let columns = this.respArrayCols.map((col) => data[col]);
      return columns;
    });
  }
  download(status: any, exportAs: any) {

    this.banzpayService.getBenzpayBasedOnStatus(status.value).subscribe((resp) => {
      console.log(resp);
      if(resp){
      this.dataFilter = resp;
      this.respArrayColsFilter = this.dataFilter.columnNames;
      this.getSqlDataFilter(this.dataFilter,exportAs.value);
      }
      else{
        Swal.fire({ text: "No Data available", icon: 'warning' });
        return;
      }
    });
    
  }
  async getSqlDataFilter(data,exportAs) {
    
    await new Promise((resolve) => setTimeout(resolve, 0));
    this.respArrayColsFilter = data.columnNames.filter(function (value, index, arr) {
      return value != "data";
    });
    // this.respArrayCols.splice(1, 1);
    this.formattedHeadersFilter = this.respArrayColsFilter.map((item) => {
      return item.replace(/([A-Z])/g, " $1").replace(/^./, function (str) {
        return str.toUpperCase();
      });
    });
    this.respArrayFilter = data.columnData.banzpayInfoList.map((item) => {
      const { data, ...rest } = item;
      return rest;
    });
    this.constructDynamicRowsFilter(exportAs);
      this.dtTrigger.next();
   
  }
  constructDynamicRowsFilter(exportAs) {
    this.rowDataFilter = this.respArrayFilter.map((data) => {
      let columnsFilter = this.respArrayColsFilter.map((col) => data[col]);
      return columnsFilter;
    });
    exportAs === "Pdf" ? this.downloadPdf() : this.downloadExcel();
  }
  downloadPdf() {
    
    let doc = new jsPDF();
    doc.autoTable({
      
      head: [this.formattedHeadersFilter],
      body: this.rowDataFilter,
      horizontalPageBreak: true
    })
    doc.save("banzpaySqlData.pdf");
  }
  downloadExcel() {
    var today = new Date();
    this.excelService.generateExcel(
      [this.formattedHeadersFilter],
      [this.rowDataFilter],
      today
    );
  }
}