import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { BanzpaySqlDataComponent } from './banzpay-sql-data/banzpay-sql-data.component';
const routes: Routes = [
  {
    path:"",
    component:BanzpaySqlDataComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class BanzpaySqlDataRoutingModule { }
