import { Injectable } from "@angular/core";
import { Workbook } from "exceljs";
import * as fs from "file-saver";

@Injectable({
  providedIn: "root",
})
export class BanzpayExcelService {
  constructor() {}
  async generateExcel([formattedHeaders], [rowData], today) {
    const title = "Banzpay Sql Data";
    const header = formattedHeaders;
    const workbook = new Workbook();
    const worksheet = workbook.addWorksheet("Sharing Data");
    const titleRow = worksheet.addRow([title]);
    titleRow.font = {
      name: "Corbel",
      family: 4,
      size: 16,
      underline: "double",
      bold: true,
    };
    worksheet.addRow([]);
    worksheet.addRow([]);
    const subTitleRow = worksheet.addRow([
      "Date" +
        ":" +
        today.getFullYear() +
        "-" +
        (today.getMonth() + 1) +
        "-" +
        today.getDate(),
    ]);

    worksheet.mergeCells("A1:D2");
    worksheet.addRow([]);
    const headerRow = worksheet.addRow(header);
    headerRow.eachCell((cell, number) => {
      cell.fill = {
        type: "pattern",
        pattern: "solid",
        fgColor: { argb: "FFFFFF00" },
        bgColor: { argb: "FF0000FF" },
      };
      cell.border = {
        top: { style: "thin" },
        left: { style: "thin" },
        bottom: { style: "thin" },
        right: { style: "thin" },
      };
    });
    rowData.forEach((d) => {
      const row = worksheet.addRow(d);
      const qty = row.getCell(5);
      let color = "";
    });
    [...Array(formattedHeaders.length).keys()].map((number) => {
      worksheet.getColumn(number + 1).width = 40;

    })
    worksheet.addRow([]);
    const footerRow = worksheet.addRow([
      "This is system generated excel sheet.",
    ]);
    footerRow.getCell(1).fill = {
      type: "pattern",
      pattern: "solid",
      fgColor: { argb: "FFCCFFE5" },
    };
    footerRow.getCell(1).border = {
      top: { style: "thin" },
      left: { style: "thin" },
      bottom: { style: "thin" },
      right: { style: "thin" },
    };
    worksheet.mergeCells(`A${footerRow.number}:C${footerRow.number}`);
    workbook.xlsx.writeBuffer().then((data: any) => {
      const blob = new Blob([data], {
        type: "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet",
      });
      fs.saveAs(blob, "banzpaySqlData");
    });
  }
}
